export interface IRecordGridColumnNamedFilterOptionDesign {
  filterExpression: string;
  index: number;
  title: string;
}

export interface IRecordGridColumnNamedFilterOption extends IRecordGridColumnNamedFilterOptionDesign {
  guid: string;
}

export interface IRecordGridNamedFilterOptionsMap {
  [fieldId: string]: IRecordGridColumnNamedFilterOption[];
}

// Adapt dropped moment and changed the signature of AdvancedFilterTimeDataType from [moment.Moment, moment.Moment] to [RxDatetimeStruct, RxDatetimeStruct].
// We need to reinstate the [moment.Moment, moment.Moment] signature since we rely on many moment methods in our code.
export type RxAdvancedFilterDateDataType = [moment.Moment, moment.Moment];

export interface IGridViewPresetProperties {
  viewPresetGuid: string;
  filters: string;
}

export interface IGridViewColumnProperties {
  fieldId: string;
  index: number;
  visible: boolean;
  sortable: IRecordGridSortableObject | null;
  width?: string;
}

export interface IRecordGridSortableObject {
  direction: ColumnSortDirection;
}

export enum ColumnSortDirection {
  Asc = 'asc',
  Desc = 'desc'
}
